/* 1. Global - Set up the things
/* ---------------------------------------------------------- */

@import "global.css";
body {
	font-family: 'IBM Plex Sans Condensed', sans-serif;
	/* font-family: 'Space Mono', monospace; */
}

p {
	font-family: 'IBM Plex Sans Condensed', sans-serif;
}

.img {
	display: block;
	width: 100%;
	height: 100%;
	background-position: center center;
	background-size: cover;
	border-radius: 100%;
}

.hidden {
	visibility: hidden;
	position: absolute;
	text-indent: -9999px;
}

/* 2. Layout - Page building blocks
/* ---------------------------------------------------------- */

#particles-js {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
	-ms-flex-pack: center;
	justify-content: center;
	position: relative;
	width: 100%;
	height: 100vh;
	-ms-flex-align: center;
	align-items: center;
	background-color: #fff;
}

#particles-js h1,
.site-logo {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: white;
	font-weight: normal;
}

.button--explore {
	position: absolute;
	bottom: 30px;
	left: 50%;
	z-index: 11;
	border: none;
	background-color: transparent;
	color: hsla(0, 0%, 0%, .3);
	text-align: center;
	text-transform: lowercase;
	letter-spacing: 0px;
	font-weight: 100;
	font-size: 2rem;
	transition: all .3s ease;
	transform: translateX(-50%);
	animation: 5s fader infinite;
}

.chevon {
	display: block;
	fill: white;
	fill: #444;
	margin: 1rem auto 0;
	width: 15px;
}

[role="button"] {
	cursor: pointer;
}

/* Animations - Fade */

@keyframes fader {
	0% {
		color: hsla(0, 0%, 100%, .3)
	}
	33% {
		color: #6a6a6a
	}
	66% {
		color: #707070
	}
	to {
		color: hsla(0, 0%, 100%, .3)
	}
}

.site-wrapper {
	/* background: #33636;
	background-color: #f3f3f3; */
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}

.site-main {
	z-index: 100;
	flex-grow: 1;
}

/* Full width page blocks */

.outer {
	position: relative;
	padding: 0 4vw;
}

/* Centered content container blocks */

.inner {
	margin: 0 auto;
	max-width: 1040px;
	width: 100%;
}

/* Usage:

<div class="outer">
	<div class="inner">
		Centered content
	</div>
</div>

*/

/* 3. Special Template Styles
/* ---------------------------------------------------------- */

@media (min-width: 900px) {
	.home-template .post-feed,
	.tag-template .post-feed,
	.author-template .post-feed {
		margin-top: -70px;
		padding-top: 0;
	}
	.site-nav {
		margin: 0 auto;
		position: relative;
		padding: 1rem 0 5rem;
	}
	/* Keeps the intro page working properly */
	.sticky+main .intro {
		margin: 60px 0 0;
	}
}

/* 4. Site Header
/* ---------------------------------------------------------- */

.site-header {
	position: relative;
	padding-top: 12px;
	padding-bottom: 12px;
	color: #fff;
	background: color(var(--darkgrey) l(-5%)) no-repeat center center;
	background-size: cover;
}

.site-header:before {
	content: "";
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 10;
	display: block;
	background: rgba(0, 0, 0, 0.18);
}

.site-header:after {
	content: "";
	position: absolute;
	top: 0;
	right: 0;
	bottom: auto;
	left: 0;
	z-index: 10;
	display: block;
	height: 80px;
	background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0));
}

.site-header.no-cover:before,
.site-header.no-cover:after {
	display: none;
}

.site-header-content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 10vw 4vw;
	min-height: 200px;
	max-height: 450px;
	text-align: center;
}

.site-title {
	z-index: 10;
	margin: 0;
	padding: 0;
	font-size: 3.8rem;
	font-weight: 700;
}

.site-logo {
	max-height: 20rem;
}

.site-description {
	z-index: 10;
	margin: 0;
	padding: 5px 0;
	font-size: 2.2rem;
	font-weight: 300;
	letter-spacing: 0.5px;
	opacity: 0.8;
}

@media (max-width: 500px) {
	.site-title {
		font-size: 3rem;
	}
	.site-description {
		font-size: 1.8rem;
	}
}

/* 5. Site Navigation
/* ---------------------------------------------------------- */

.site-nav,
.site-nav--wrapper {
	z-index: 300;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	/* overflow-y: hidden; */
	height: 40px;
	font-size: 1.4rem;
	/* background-color: rgba(55, 54, 54, 0.75); */
}

.page-template .site-nav {
	position: fixed;
	width: 100%;
}

.site-nav--wrapper {
	margin: 0 auto;
}

.site-nav--wrapper {
	display: flex;
}

/* .home-template .site-nav {
	border-bottom: 1px solid #010729;
	position: relative;
}  */

.page-template .site-nav {
	position: fixed;
}

/* Site Nav Hack Explanation (above):

What's happening above it .site-nav-left is set to overflow-x and allow sideways scrolling, so that when there isn't enough space for all nav items (either due to lots of nav items, or a small viewport), you can still scroll side-to-side to reach them.

The knock-on effect of this is ugly browser-scroll bars at the bottom, so 80px of padding-bottom and a 40px fixed height parent (.site-nav) hides that entirely. Slightly hacky code. But nice clean end-result.

*/

.site-nav-logo {
	flex-shrink: 0;
	display: block;
	margin-right: 24px;
	/* padding: 11px 0;
	color: #fff;
	font-size: 1.7rem;
	line-height: 1em;
	font-weight: bold;
	letter-spacing: -0.5px; */
}

.site-nav-logo:hover {
	text-decoration: none;
}

.site-nav-logo img {
	display: block;
	width: auto;
	height: 32px;
}

.nav {
	display: flex;
	margin: 0 0 0 -12px;
	padding: 0;
	list-style: none;
}

.nav li {
	display: block;
	margin: 0;
	padding: 0;
	text-transform: uppercase;
}

.nav li a {
	display: inline-block;
	margin: 2px 0 0;
	height: 25px;
	padding: 0 12px;
	color: #444;
	opacity: 0.8;
}

.nav li a:after {
	content: '';
	display: block;
	margin: 2px auto 0;
	height: 1px;
	width: 0;
	background: transparent;
	transition: width 0.2s ease, background-color .5s ease;
}

.nav li a:hover:after {
	width: 100%;
	background-color: #444;
}

.nav li a:hover {
	text-decoration: none;
	opacity: 1;
}

.site-nav-right {
	flex-shrink: 0;
	display: flex;
	align-items: center;
	height: 40px;
}

.social-links {
	flex-shrink: 0;
	display: flex;
	align-items: center;
}

.social-link {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0;
	padding: 10px;
	color: #fff;
	opacity: 0.8;
}

.social-link:hover {
	opacity: 1;
}

.social-link svg {
	/* height: 1.8rem; */
	/* fill: #fff; */
	fill: #444;
}

.social-link-wb svg path {
	stroke: #fff;
}

.social-link-rss svg {
	height: 1.9rem;
}

.subscribe-button {
	display: block;
	padding: 4px 10px;
	border: #fff 1px solid;
	color: #fff;
	font-size: 1.2rem;
	line-height: 1em;
	border-radius: 10px;
	opacity: 0.8;
}

.subscribe-button:hover {
	text-decoration: none;
	opacity: 1;
}

.rss-button {
	opacity: 0.8;
}

.rss-button:hover {
	opacity: 1;
}

.rss-button svg {
	margin-bottom: 1px;
	height: 2.1rem;
	fill: #fff;
}

@media (max-width: 700px) {
	.site-header {
		padding-right: 0;
		padding-left: 0;
	}
	/*.site-nav-left {
		margin-right: 0;
		padding-left: 4vw;
	}
	.site-nav-right {
		display: none;
	} */
}

/* 6. Post Feed
/* ---------------------------------------------------------- */

.post-feed {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	margin: 0 -20px;
	padding: 40px 0 0 0;
}

.post-card {
	flex: 1 1 300px;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	margin: 0 20px 40px;
	min-height: 300px;
	background: #fff center center;
	background-size: cover;
	border-radius: 5px;
	box-shadow: rgba(39, 44, 49, 0.06) 8px 14px 38px, rgba(39, 44, 49, 0.03) 1px 3px 8px;
	transition: all 0.5s ease;
}

.post-card:hover {
	box-shadow: 0 0 1px rgba(39, 44, 49, 0.10), 0 3px 16px rgba(39, 44, 49, 0.07);
	transition: all 0.3s ease;
	transform: translate3D(0, -1px, 0);
}

.post-card-image-link {
	position: relative;
	display: block;
	overflow: hidden;
	border-radius: 5px 5px 0 0;
}

.post-card-image {
	width: auto;
	height: 200px;
	background: var(--lightgrey) no-repeat center center;
	background-size: cover;
}

.post-card-content-link {
	position: relative;
	display: block;
	padding: 25px 25px 0;
	color: var(--darkgrey);
}

.post-card-content-link:hover {
	text-decoration: none;
}

.post-card-tags {
	display: block;
	margin-bottom: 4px;
	color: var(--midgrey);
	font-size: 1.2rem;
	line-height: 1.15em;
	font-weight: 500;
	letter-spacing: 0.5px;
	text-transform: uppercase;
}

.post-card-title {
	margin-top: 0;
}

.post-card-content {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.post-card-excerpt {
	font-family: Georgia, serif;
}

.post-card-meta {
	padding: 0 25px 25px;
}

.author-profile-image {
	margin-right: 5px;
	width: 25px;
	height: 25px;
	border-radius: 100%;
	object-fit: cover;
}

.post-card-author {
	font-size: 1.3rem;
	font-weight: 500;
	letter-spacing: 0.5px;
	text-transform: uppercase;
}

/* Special Styling for home page grid (below):

The first (most recent) post in the list is styled to be bigger than the others and take over the full width of the grid to give it more emphasis. Wrapped in a media query to make sure this only happens on large viewports / desktop-ish devices.

 */

@media (min-width: 795px) {
	.home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) {
		flex: 1 1 100%;
		flex-direction: row;
	}
	.home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-image-link {
		position: relative;
		flex: 1 1 auto;
		border-radius: 5px 0 0 5px;
	}
	.home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-image {
		position: absolute;
		width: 100%;
		height: 100%;
	}
	.home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-content {
		flex: 0 1 357px;
	}
	.home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) h2 {
		font-size: 2.6rem;
	}
	.home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) p {
		font-size: 1.8rem;
		line-height: 1.55em;
	}
	.home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-content-link {
		padding: 30px 40px 0;
	}
	.home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-meta {
		padding: 0 40px 30px;
	}
}

.home-template .site-header:after {
	display: none;
}

/* Adjust some margins for smaller screens */

@media (max-width: 650px) {
	.post-feed {
		padding-top: 5vw;
	}
	.post-card {
		margin: 0 20px 5vw;
	}
}

